.background_gray {
  width: 100%;
  height: 100%;
  background: #f7f7f7;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.my_bottom {
  height: 1rem;
  width: 100%;
}
.editHotel {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%;
}
.editHotel .bottom {
  height: 0.96rem;
  background: #19ad19;
  font-size: 0.32rem;
  color: white;
  text-align: center;
  line-height: 0.96rem;
}
.modal_main {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  overflow-y: auto;
}
.border_none {
  border: none!important;
}
.border_base_bottom {
  border-bottom: 1px solid #C7C7C7 !important;
}
.item_title {
  font-family: PingFang SC;
  width: 100%;
  height: 0.72rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0.15rem 0.2rem;
  font-size: 0.28rem;
  line-height: 0.4rem;
  color: #000000;
  border-bottom: 1px solid #C7C7C7;
}
.item_title a {
  float: right;
  font-size: 0.28rem;
  color: #1aad19;
}
.setting_item_two_line {
  font-family: PingFang SC;
  width: 100%;
  height: 1.4rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0.15rem 0.2rem;
  font-size: 0.28rem;
  color: #000000;
  background: #fff;
  border-bottom: 1px solid #C7C7C7;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.setting_item_two_line .content {
  line-height: 1;
  margin-bottom: 0.2rem;
}
.setting_item_two_line .time {
  color: #9b9b9b;
  font-size: 0.28rem;
  line-height: 0.4rem;
  text-align: left;
}
.setting_item_two_line .info.orange {
  color: #fe9e1e;
}
.setting_item {
  font-family: PingFang SC;
  width: 100%;
  height: 1rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0.15rem 0.2rem;
  font-size: 0.28rem;
  line-height: 0.4rem;
  color: #000000;
  background: #fff;
  border-bottom: 1px solid #C7C7C7;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.setting_item .password {
  width: 80%!important;
  position: relative;
}
.setting_item .password .password_group {
  width: 100%!important;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-right: 0.6rem;
}
.setting_item .password .iconfont {
  position: absolute;
  right: 0rem;
  bottom: 0.25rem;
  font-size: 0.5rem;
  color: #999;
}
.setting_item:last-child {
  border-bottom: none;
}
.setting_item.setting_item_40 {
  height: 0.8rem;
}
.setting_item.setting_item_big {
  height: 1.6rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.setting_item.setting_item_big > div {
  text-align: left;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.setting_item.setting_item_big > div .iconfont {
  position: absolute;
  right: 0rem;
  bottom: 0.25rem;
  font-size: 0.3rem;
  color: #999;
}
.setting_item.setting_item_big > div.address {
  width: 100%!important;
  position: relative;
}
.setting_item.setting_item_big > div.address .address_group {
  width: 100%!important;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
