}
.setting_item svg {
  width: 0.4rem;
  height: 0.4rem;
}
.setting_item .info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.setting_item .info span:first-child {
