}
.setting_item .info span:first-child img {
  width: 0.68rem;
  height: 0.68rem;
  border-radius: 50%;
}
.setting_item .info.info_switch {
  height: 0.5rem;
}
.setting_item .info .removeBind {
  font-size: 0.28rem;
  color: #1aad19;
  text-decoration: underline;
  margin-left: 0.1rem;
}
/* 图片 */
.upload {
  padding: 0.1rem 0.2rem;
  background: #fff;
}
.upload p {
  text-align: left;
  font-size: 0.28rem;
}
.upload p span {
  color: #898989;
}
.upload .imgs {
  padding: 0.1rem 0;
  text-align: left;
  /*display: flex;
    flex-wrap: wrap;*/
}
.upload .imgs .upload_button {
  display: inline-block;
  margin-left: 0.16rem;
}
.upload .imgs .pics {
  position: relative;
  display: inline-block;
  width: 1.62rem;
  margin-left: 0.18rem;
}
.upload .imgs .pics .uploadPics {
  width: 1.6rem;
  border-radius: 0.1rem;
}
.upload .imgs .pics:nth-child(1),
.upload .imgs .pics:nth-child(5) {
  margin-left: 0;
}
.upload .imgs .pics:nth-child(5),
.upload .imgs .pics:nth-child(6),
.upload .imgs .pics:nth-child(7) {
  margin-top: 0.16rem;
}
.upload .imgs .pics .deleteBtn {
  position: absolute;
  right: 0;
  top: -0.17rem;
  font-size: 0.34rem;
  color: #d7d7d7;
}
.submit {
  padding: 0 0.3rem;
  padding-top: 1rem;
}
.submit button {
  width: 100%;
  height: 0.8rem;
  font-size: 0.32rem;
  color: #fff;
  border-radius: 0.4rem;
}
.my_amap_container {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.my_amap_container .serch {
  background: #fff;
  width: 100%;
  height: auto;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0.24rem 0.4rem;
}
.my_amap_container .el-vue-search-box-container {
  position: relative;
  height: 0.6rem;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  top: 0;
  left: 0;
  font-size: 0.28rem;
}
.my_amap_container .el-vue-search-box-container .search-box-wrapper {
  background: #efefef;
}
.my_amap_container .el-vue-search-box-container .search-box-wrapper input {
  height: 100%;
  background: #efefef;
  font-size: 0.28rem;
}
.my_amap_container .el-vue-search-box-container .search-box-wrapper .search-btn {
  width: 1rem;
}
.my_amap_container .el-vue-search-box-container .search-tips {
  width: 100%;
}
.my_amap_container .el-vue-search-box-container .search-tips li {
  height: 0.6rem;
  font-size: 0.28rem;
  color: #666;
  line-height: 0.6rem;
}
.el-vue-amap-container.amap-demo {
  width: 100%;
  height: calc(100% - 1.08rem);
  position: relative;
  font-size: 0.28rem;
}
.toolbar {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 1.2rem;
  background: #fff;
  width: 100%;
  font-size: 0.28rem;
  text-align: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.toolbar .iconfont {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 100%;
  text-align: right;
  padding-right: 0.1rem;
}
.toolbar .iconfont i {
  font-size: 0.4rem;
  line-height: 1rem;
  color: #e5544c;
}
.toolbar .address_text {
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
  height: 100%;
  text-align: left;
  font-size: 0.28rem;
  padding-top: 0.15rem;
}
.toolbar .address_text .text-ellipsis {
  width: 5.4rem;
  color: #9b9b9b;
  font-size: 0.24rem;
}
.search-box {
  position: absolute;
  top: 25px;
  left: 20px;
}
.amap-page-container {
  position: relative;
}
.my_bottom .addAddress {
  background: #e5544c;
  height: 1rem;
  width: 100%;
  color: #fff;
  line-height: 1rem;
  font-size: 0.28rem;
}



