
.goodsAdd .weui-cell {
  padding: 0 0.2rem!important;
}
.goodsAdd .weui-label {
  width: auto!important;
  height: 1rem;
  line-height: 1rem;
  font-size: 0.28rem;
  padding-right: 0.4rem;
}
.goodsAdd .weui-input {
  height: 1rem!important;
  line-height: 1rem!important;
  text-align: right;
  font-size: 0.28rem;
}
.goodsAdd .weui-cell_select-after .weui-select {
  font-size: 0.28rem;
}
.goodsAdd .weui-cell_select .weui-cell__bd:after {
  height: 0.24rem!important;
  width: 0.24rem!important;
  top: 40%!important;
}
.goodsAdd .weui-cells {
  margin-top: 0!important;
}
.goodsAdd .switchLine .weui-cell__bd {
  -webkit-box-flex: 0!important;
      -ms-flex: none!important;
          flex: none!important;
}
.goodsAdd .switchLine .weui-cell__ft {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.goodsAdd .switchLine .weui-switch {
  -webkit-transform:scale(1.5,1.5);
          transform:scale(1.5,1.5);
}
.goodsAdd .ggz .weui-cell__bd {
  padding-right: 0.3rem;
}



