  color: white;
}
.iosHeader {
  width: 100%;
  height: 1.28rem;
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 0.32rem;
  padding: 0 0.2rem 0.24rem 0.2rem;
  position: relative;
}
.iosHeader svg {
  width: 0.5rem;
  height: 0.5rem;
}
.iosHeader .left {
  position: absolute;
  left: 0.2rem;
}
.iosHeader .right {
  position: absolute;
  right: 0.2rem;
}
.iosHeader .headericon {
  font-size: 0.3rem;
}
.weui-toast {
  top: 3.8rem !important;
}



